import { accessToken, styles, lang, latt } from "./global"
import * as mapboxgl from 'mapbox-gl';
// import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder';
// import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css';
const three = require('threebox-plugin');

let mapGL: mapboxgl.Map;
let threebox: any;

export function initializeMap() {
  mapboxgl as typeof mapboxgl;

  mapGL = new mapboxgl.Map({
    accessToken: accessToken,
    container: 'map',
    style: styles.find(style => style.name == 'tangkak')?.url,
    zoom: 18,
    center: { lng: lang, lat: latt },
    bearing: 210,
    pitch: 60,
    // style: 'mapbox://styles/mapbox/light-v11',
    // center: { lng: -73.97627, lat: 40.75155 },
    // zoom: 15.4,
    // pitch: 64.9,
    // bearing: 172.5,
    antialias: true
  });
  mapGL.addControl(new mapboxgl.NavigationControl());

  // Add the control to the map.

  // eslint-disable-next-line no-undef
  threebox = ((<any>window).tb = new three.Threebox(
    mapGL, mapGL.getCanvas().getContext('webgl'), { defaultLights: true }
  ));
  mapGL.on('load', () => {
    // this.initMarker();
    // this.initLayer();
  });
  mapGL.on('style.load', () => {
    init3d(mapGL);
    initThreeGLBF(mapGL);
    initPopup(mapGL);
  });
}

export function initPopup(mapGL: mapboxgl.Map) {
  const popup = new mapboxgl.Popup({ closeOnClick: false })
    .setLngLat([102.52873755414117, 2.271506810998862])
    .setHTML('<div><h4>Sekolah Kebangsaan Ledang</h4><img class="school" src="./johor-galeri.map/assets/sekolah-ledang.png" /></div>')
    .addTo(mapGL);
}

export function init3d(mapGL: mapboxgl.Map) {
  // Insert the layer beneath any symbol layer.
  const layers: any[] = mapGL.getStyle().layers;
  const labelLayerId = layers.find((layer) => layer.type === 'symbol' && layer.layout['text-field']).id;

  // The 'building' layer in the Mapbox Streets vector 
  // tileset contains building height data from OpenStreetMap.
  mapGL.addLayer(
    {
      'id': 'add-3d-buildings',
      'source': 'composite',
      'source-layer': 'building',
      'filter': ['==', 'extrude', 'true'],
      'type': 'fill-extrusion',
      'minzoom': 15,
      'paint': {
        'fill-extrusion-color': '#aaa',
        // Use an 'interpolate' expression to
        // add a smooth transition effect to
        // the buildings as the user zooms in.
        'fill-extrusion-height': [
          'interpolate',
          ['linear'],
          ['zoom'],
          15,
          0,
          15.05,
          ['get', 'height']
        ],
        'fill-extrusion-base': [
          'interpolate',
          ['linear'],
          ['zoom'],
          15,
          0,
          15.05,
          ['get', 'min_height']
        ],
        'fill-extrusion-opacity': 0.6
      }
    },
    labelLayerId
  );
}

export function initThreeGLBF(mapGL: mapboxgl.Map) {
  mapGL.addLayer({
    id: 'custom-threebox-model-1',
    type: 'custom',
    renderingMode: '3d',
    onAdd: function () {
      // Creative Commons License attribution:  Metlife Building model by https://sketchfab.com/NanoRay
      // https://sketchfab.com/3d-models/metlife-building-32d3a4a1810a4d64abb9547bb661f7f3
      const scale = 1.2;
      const options = {
        obj: 'https://docs.mapbox.com/mapbox-gl-js/assets/metlife-building.gltf',
        type: 'gltf',
        scale: { x: scale, y: scale, z: 1.1 },
        units: 'meters',
        rotation: { x: 90, y: -10, z: 0 }
      };
      threebox.loadObj(options, (model: any) => {
        //2.269285668423803, 102.52782710861818
        model.setCoords([102.5298, 2.26985]);
        model.setRotation({ x: 0, y: 0, z: 241 });
        threebox.add(model);
      });
    },
    render: function () {
      threebox.update();
    }
  });
  mapGL.addLayer({
    id: 'custom-threebox-model-mpt',
    type: 'custom',
    renderingMode: '3d',
    onAdd: function () {
      // Creative Commons License attribution:  Metlife Building model by https://sketchfab.com/NanoRay
      // https://sketchfab.com/3d-models/metlife-building-32d3a4a1810a4d64abb9547bb661f7f3
      const scale = 2.5;
      const options = {
        obj: 'assets/40_MajlisPenbandaranTangkak.glb',
        type: 'glb',
        scale: { x: scale, y: scale, z: 2.8 },
        units: 'meters',
        rotation: { x: 90, y: -267, z: 0 }
      };
      threebox.loadObj(options, (model: any) => {
        model.setCoords([lang, latt]);
        model.setRotation({ x: 0, y: 0, z: 241 });
        threebox.add(model);
      });
    },
    render: function () {
      threebox.update();
    }
  });
}